export default {
	data() {
		return {
			remind:[],
			pass:[],
			signIn:[]
		}
	},
	onShow() {
		this.getToken()
	},
	methods: {
		//跳转签到
		toSign(meetingId){
			uni.navigateTo({
				url:'./sign/sign?meetingId='+meetingId
			})
		},
		//获取token
		getToken(){
			var that = this
			uni.getStorage({
				key:'token',	
				success: function (res) {
				that.token = res.data.token
				that.getDetail()
				},
			})
		},
		//获取消息列表
		getDetail(){
			var that = this;
			uni.request({
				url:'http://112.74.86.165:8082/message',
				method:'GET',
				header:{
					Authorization:that.token
				},
				success(res) {
					console.log(res)
					that.remind = res.data.data.remind
					that.pass = res.data.data.pass
					that.signIn = res.data.data.signIn
				},		
			})
		}
	}
}
